<script>
import { defineComponent } from "vue";
import { usePostsStore } from '@/stores/posts'


export default defineComponent({
	name: "PostCardComponent",
	props: ["post"],
	setup(props) {
		const goTo = (e) => {
			let post = usePostsStore();
			
			post.$patch({ post: props.post })
			
				window.location.href = "/#" + props.post.url;
			console.log("/#" + props.post.url);
		};
		
		const lazyLoad = function (e) {
			if (e.target.getBoundingClientRect().top < window.innerHeight) {
				e.target.src = e.target.dataset.src;
				e.target.classList.remove("lazy");
			}
		};
		
		return {
			props,
			lazyLoad,
			goTo,
		};
	},
});
</script>

<template>
	<div @click="goTo(props.post.id)" class="container-post d-flex flex-column justify-content-end" :style="{
		aspectRatio: 1 / 1,
		cursor: 'pointer',
	}">
	<img class="col-12" @load="lazyLoad($event)" :src="'/card.webp'" :data-src="props.post.img" loading="lazy" alt="" />
	
	<div class="bg-light border border-2 px-2">
		<p>
			<span class="fw-bold">{{ props.post.text }}</span>
		</p>
		<p>
			<span>{{ props.post.textSecundary }}</span>
		</p>
	</div>
</div>
</template>
<style scoped>
.container-post {
	background-size: contain;
}
</style>
